.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 10;
    background: rgba(0, 0, 0, 0.75);
  }
  
  .board {
    background: white;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
    border-radius: 10px;
  }
  
  .modal {
    position: relative;
    top: 3vh;
    left: 5%;
    right: 5%;
    width: 100%;
    z-index: 100;
    overflow: hidden;
  }

  .image {
    width: 100%;
  }
  
  .header {
    background: red;
    padding: 1rem;
  }
  
  .header h2 {
    margin: 0;
    color: white;
  }
  
  .content {
    padding: 1rem;
  }
  
  .actions {
    padding: 1rem;
    display: flex;
    justify-content: flex-end;
  }
  
  @media (min-width: 600px) {
    .modal {
      left: calc(50% - 20rem);
      width: 40rem;
    }
  }
  